import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { ENTER, COMMA, SEMICOLON } from '@angular/cdk/keycodes';

@Component({
  selector: 'lib-sub-category',
  templateUrl: './sub-category.component.html',
  styleUrls: ['./sub-category.component.scss']
})
export class SubCategoryComponent implements OnInit {
  @Input() field: any
  @Input() length: any
  @Input() i: any
  @Input() j: any
  @Output() onAddKeywordFromInput = new EventEmitter<any>()
  @Output() onKeyPress = new EventEmitter<any>()
  @Output() onDrop = new EventEmitter<any>()
  @Output() onAddNewField = new EventEmitter<any>()
  @Output() onRemoveField = new EventEmitter<any>()
  @Output() onChangeType = new EventEmitter<any>()
  @Output() onRemoveKeyword = new EventEmitter<any>()
  @Output() onAddNewSubLevelField = new EventEmitter<any>()
  @Output() onExpendMore = new EventEmitter<any>()


  separatorKeysCodes = [ENTER, COMMA, SEMICOLON];
  constructor() { }

  ngOnInit(): void {
  }

  drop(event) {
    this.onDrop.emit(event)
  }

  addKeywordFromInput(event, i, j) {
    this.onAddKeywordFromInput.emit({ event, i, j })
  }

  keyUp(event, i, j) {
    this.onKeyPress.emit({ event, i, j })
  }

  addNewField(event, i) {
    event.preventDefault()
    this.onAddNewField.emit({ event, i })
  }

  removeField(event, i, j) {
    event.preventDefault()
    this.onRemoveField.emit({ i, j })
  }

  changeType(type, i, j) {
    this.onChangeType.emit({ type, i, j })
  }

  removeKeyword(event, i, j) {
    this.onRemoveKeyword.emit({ event, i, j })
  }
  addNewSubLevelField(e) {
    e.preventDefault()
    this.onAddNewSubLevelField.emit(true)
  }
  expendMore(e) {
    e.preventDefault()
    this.onExpendMore.emit(true)
  }

  ingoreDefaultAction(e) {
    e.preventDefault()
  }

}
